(function() {
  'use strict';

  angular
    .module('nikitabuyevichCom', ['ngAnimate', 'ngCookies', 'ngTouch', 'ngSanitize', 'ngMessages', 'ngAria', 'ngResource', 'ui.router', 'ui.bootstrap', 'duScroll']);

})();

(function() {
  'use strict';

  angular
    .module('nikitabuyevichCom')
    .directive('skills', skills);

  /** @ngInject */
  function skills() {
    var directive = {
      restrict: 'E',
      templateUrl: 'app/components/skills/skills.html'
    };

    return directive;
  }

})();

(function() {
  'use strict';

  angular
    .module('nikitabuyevichCom')
    .directive('projects', projects);

  /** @ngInject */
  function projects() {
    var directive = {
      restrict: 'E',
      templateUrl: 'app/components/projects/projects.html'
    };

    return directive;
  }

})();

(function() {
  'use strict';

  angular
    .module('nikitabuyevichCom')
    .directive('contact', contact);

  /** @ngInject */
  function contact() {
    var directive = {
      restrict: 'E',
      templateUrl: 'app/components/contact/contact.html'
    };

    return directive;
  }

})();

(function() {
  'use strict';

  angular
    .module('nikitabuyevichCom')
    .value('duScrollDuration', 1250)
    .directive('navbar', navbar)
    .controller('ScrollToCtrl', function($scope, $document){}).value('duScrollOffset', 10);

  /** @ngInject */
  function navbar() {
    var directive = {
      restrict: 'E',
      templateUrl: 'app/components/navbar/navbar.html',
      
    };

    return directive;

  }
})();

(function() {
  'use strict';

  angular
    .module('nikitabuyevichCom')
    .directive('about', about);

  /** @ngInject */
  function about() {
    var directive = {
      restrict: 'E',
      templateUrl: 'app/components/about/about.html'
    };

    return directive;
  }

})();

(function() {
  'use strict';

  angular
    .module('nikitabuyevichCom')
    .config(routerConfig);
  /** @ngInject */
  function routerConfig($stateProvider, $urlRouterProvider) {
    $stateProvider
      .state('home', {
        url: '/',
        templateUrl: 'app/main/main.html'
      });

    $urlRouterProvider.otherwise('/');
  }

})();

(function() {
  'use strict';

  angular
    .module('nikitabuyevichCom')
    .config(config);

  /** @ngInject */
  function config($logProvider, $locationProvider) {
    // Enable log
    if(window.history && window.history.pushState){
      $locationProvider.html5Mode({
      enabled: true,
      requireBase: false
      });
    }
  }

})();

